<template>
  <div class="login-wrap"><!---->
  <el-menu :default-active="activeIndex" :router=true class="el-menu-demo" mode="horizontal">
    <el-menu-item index="/">Welcome</el-menu-item>
    <el-menu-item index="/AllGoods" ></el-menu-item>

    <el-menu-item index="/Register" v-show="!logState" style="float:right;"></el-menu-item>
    <el-menu-item index="/User" v-show="logState" style="float:right;" ></el-menu-item>

    <el-submenu index="/AboutUs" style="float:right;" >
    <template v-slot:title></template>
     <el-menu-item index="/UpdateHistory"></el-menu-item>
    <el-menu-item index="/Register/Terms"></el-menu-item>
    <el-menu-item index="/Contactus"></el-menu-item>
    <el-menu-item index="/DataAnalysis" ></el-menu-item>
    </el-submenu>
  </el-menu>

    <div class="ms-login"><!---->
        <div class="ms-title"><!----></div>
        <el-form class="ms-content" :model="param" ref="param" :rules="rules">  
          <el-form-item prop="userMail">
              <label class="register-label">   </label>
              <el-input class="register-inputMail" v-model="param.userMail">
              <template v-slot:append>@seu.edu.cn</template>
              </el-input>   
              
          </el-form-item>


          <el-form-item prop="password">
              <label class="register-label" ></label>
              <el-input class="register-inputP1" type="password" show-password v-model="param.password">12345678
              </el-input>
          </el-form-item>

          <el-form-item prop="password2">
              <label class="register-label"></label>
              <el-input class="register-inputP2" type="password" show-password v-model="param.password2">12345678
              </el-input>
          </el-form-item>

          <el-form-item prop="verifyCode">
              <label class="register-label"></label>
              <el-input class="register-input" v-model="param.verifyCode" maxlength="4">
              </el-input>
              <el-button type="primary" v-bind:disabled="dis" @click="submitGetCode()">{{param.vcodeText}}</el-button>
          </el-form-item>


          <div class="login-btn">
              <el-button type="primary" @click="submitUP()"></el-button>
          </div>
          <div>
              <label class="register-labelTip"></label>
              <br>
              <label class="register-labelTip">8-16
                  <br>0-9
              </label>
          </div>
        </el-form>
    </div>
  </div>
</template>


<script>
import axios from 'axios';
import md5 from 'js-md5';
 export default({
     data: function(){
        var checkPassword=(rule,value,callback)=>
        {
            let pattern = /^[a-zA-Z0-9]{8,20}$/;               
            if(!pattern.test(value))
            {
                callback(new Error('8-20'));
            }
            callback();
        };
        // var checkVcode=(rule,value,callback)=>
        // {
        //     let pattern = /^[0-9]{4,4}$/;               
        //     if(!pattern.test(value))
        //     {
        //         callback(new Error('4'));
        //     }
        //     callback();
        // };
        return {
            checkedTerms:false,
            activeIndex:"/Register",
            useRouter:true,
            logState:false,
            param:{
                userMail:'',
                password:'',
                password2:'',
                verifyCode:'',
                time:0,
                
                vcodeText: '',
            },
            dis:false,
             rules: {
                userMail: [
                    { required: true, message: '', trigger: 'blur' },
                    // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
                ],
                password: [
                    // { required: true, message: '' },
                    //  { type: 'number', message: ''}
                    {required:true,validator:checkPassword, message: '',trigger:'change'},
                    { min: 8, max: 20, message: ' 8  20 ', trigger: 'blur' }
                ],
                password2:[
                    { required: true,validator:checkPassword, message: '',trigger: 'change' },
                     { min: 8, max: 20, message: ' 8  20 ', trigger: 'blur' }
                ],

                // verifyCode: [
                //     {  required: true,validator:checkVcode, message: '', trigger: 'blur'},
                //     //  { min: 4, max: 4, message: '4', trigger: 'blur' }
                // ],

                
            },

            
        };
     },
     //******************************** */
     methods:{
         submitGetCode(){
            this.$refs['param'].validate((valid) => {
          if (valid) 
          {
                // if(this.param.password==this.param.password2)
                // {
                var mailInfo = {
                "userMail":this.param.userMail+'@seu.edu.cn',
                };
                axios
                .post(process.env.URL_PATH+"/User/ResetPasswordGetVcode", mailInfo)
                .then(
                    (res)=>{
                        if(res.data['status']=="True")
                        {
                            this.$message.success("");  
                            this.param.time=120;
                            this.dis=true;
                            this.countTimeDown();
                        }
                        else if(res.data['status']=="NotExistsUser")
                        {
                            this.$message.error("");  
                        }else if(res.data['status']=="Error")
                        {
                            this.$message.error("");  
                        }
                    })
                .catch(
                    (err)=>{
                    console.log(err);
                    this.$message.error("");
                    });     
                // }else
                // {
                //     this.$message.error("");
                // }     
            }else{
                this.$message.error("");
            }
         });           
         },
         countTimeDown(){
             if(this.param.time>0){
                 this.param.time--;
                 this.param.vcodeText='('+this.param.time+')';
                 setTimeout(this.countTimeDown, 1000);
             }else{
                this.param.time = 0;
                this.param.vcodeText = '';
                this.dis = false;
             }
         },
         submitUP(){
             //this.$message.error("");
                   this.$refs['param'].validate((valid) => {
                    if (valid) 
                    {
                        if(this.param.password==this.param.password2)
                        {
                            let pattern = /^[0-9]{4,4}$/;               
                            if(!pattern.test(this.param.verifyCode))
                            {
                                this.$message.error("");
                                return;
                            }
                            var mailInfo = {
                            "userMail":this.param.userMail+'@seu.edu.cn',
                            'vcode':this.param.verifyCode,
                            'newPassword':md5(this.param.password)+'piao',
                            'token':md5(this.param.userMail+'@seu.edu.cn')+'piao',
                            };
                            axios
                            .post(process.env.URL_PATH+"/User/ResetPassword", mailInfo)
                            .then(
                                (res)=>{
                                    console.log(res.data['status']);
                                    if(res.data['status']=="True")
                                    {
                                        this.$message.success("");  
                                        this.$router.push("/");
                                    }
                                    else if(res.data['status']=="NotExistsUser")
                                    {
                                        this.$message.error("");  
                                    }else if(res.data['status']=="vcodeError")
                                    {
                                        this.$message.error("");  
                                    }else if(res.data['status']=="serverError"||res.data['status']=="Error")
                                    {
                                        this.$message.error("");  
                                    }else if(res.data['status']=="unfindError")
                                    {
                                        this.$message.error("");  
                                    }
                                })
                            .catch(
                                (err)=>{
                                console.log(err);
                                this.$message.error("");
                                });     
                        }else
                        {
                            this.$message.error("");
                        }     
                    }else{
                        this.$message.error("");
                    }
            });          
         },
     },
     mounted(){
         this.logState=sessionStorage.getItem("logState");
     }
 })

</script>

<style scoped>

.headStyle{
background-color:rgba(225,255,255,0.6);
width:100%;
height:40px;
}
.welcomeButtonStyle{
background-color:rgba(225,255,255,0.6);
color:rgba(0,255,127,0.9);
text-align:center;
font-size:30px;
height:100%;
}
.sideBarButt{
height:40px;
font-size:16px;
}

.login-wrap{
    position: relative;
    width: 100%;
    height: 100%;
    
    background-size: 100%;
}
.ms-title{
    width: 100%;
    line-height: 50px;
    text-align: center;
    font-size: 20px;
    color:#fff;
    border-bottom: 1px solid #ddd;
}

.ms-login{
    position: absolute;
    left: 50%;
    top:  50%;
    width: 400px;
    margin: -190px 0 0 -175px;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.3);
    overflow: hidden;
}

.ms-content{
    padding: 30px 30px;
}



.register-input{
    width: 30%;
    left:0px;
}

.register-inputMail{
    width: 70%;
    left:6px;
}


.register-inputName{
    width: 70%;
    left:30px;
}

.register-inputP1{
    width: 70%;
    left:32px;
}

.register-inputP2{
    width: 70%;
    left:15px;
}

.register-label{
    width: 10%;  
}

.register-labelTip{
    width: 20%;  
    font-size: 10px;
    color: rgba(255, 0, 0, 1);
}

.login-btn{
    text-align: center;
}
.login-btn button{
    width: 40%;
    height: 36px;
    margin-bottom: 10px;
    font-size: 14px;
}

</style>
