<template>

  <div class="img_box">
    <!--  -->
  <el-menu :default-active="activeIndex" :router=true class="el-menu-demo" mode="horizontal">
    <el-menu-item index="/">Welcome</el-menu-item>
    <el-menu-item index="/AllGoods" ></el-menu-item>

    <el-menu-item index="/Register" v-show="!logState" style="float:right;"></el-menu-item>
    <el-menu-item index="/User" v-show="logState" style="float:right;" ></el-menu-item>

    <el-submenu index="/AboutUs" style="float:right;" >
    <template slot="title"></template>
     <el-menu-item index="/UpdateHistory"></el-menu-item>
    <el-menu-item index="/Register/Terms"></el-menu-item>
    <el-menu-item index="/Contactus"></el-menu-item>
    <el-menu-item index="/DataAnalysis" ></el-menu-item>
    </el-submenu>
  </el-menu>
        <div class="tipStyle" >
         <span style="font-size=22px;left:20%;">()<br></span>
      <br>
      </div>
            <el-menu class="sideMenuCss"
              :default-active="menuIndex"
              
              background-color="#545c64"
              text-color="#fff"
              active-text-color="#ffd04b">
             <el-menu-item index="1" @click="handleSelect(1)" >
               <!-- @mouseover="handleHover()" @mouseleave="handleLeave()" -->
              <span  style="text-align: center;display:block;overflow: hidden;white-space:nowrap;">
                <el-avatar size="large" 
                  src="https://seusupermarket.cn:443/staticFile/Logo.png"></el-avatar>
              </span>
               </el-menu-item>
              <el-menu-item index="2" >
                <i class="el-icon-document"></i>
                <span slot="title"></span>
              </el-menu-item>
              <el-menu-item index="3" @click="handleSelect(3)">
                <template slot="title">
                  <i class="el-icon-circle-plus-outline"></i>
                  <span></span>
                </template>
              </el-menu-item>

              <el-menu-item index="4" @click="handleSelect(4)">
                <i class="el-icon-user"></i>
                <span slot="title"></span>
              </el-menu-item>

              <el-menu-item index="5"  @click="handleSelect(5)">
                <i class="el-icon-switch-button"></i>
                <span slot="title"></span>
              </el-menu-item>
            </el-menu>
        <!--          v-loading.fullscreen.lock="fullscreenUpLoading" -->
        <el-dialog class="detailStyle" title="" :visible.sync="showAddGoods" :modal="true"
         @close="handleDialogClose(1)" :close-on-click-modal="false"

         >
                
         
         <!-- <span><br></span> -->
         <!-- <el-form-item label="" >
            </el-form-item> -->
          <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" >
            <el-form-item  >
              <div></div>
            </el-form-item>
            <el-form-item label="" prop="name">
              <el-input v-model="ruleForm.name" maxlength="50" show-word-limit></el-input>
            </el-form-item>
            <el-form-item label="" prop="goodsCampus">
               <el-select v-model="ruleForm.goodsCampus"   placeholder="" >
                <el-option
                  v-for="item in campusArray"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="price" >
              <el-input v-model="ruleForm.price" maxlength="8" show-word-limit
               
                ></el-input>
            </el-form-item>

            <el-form-item label="" prop="contact">
              <el-input v-model="ruleForm.contact" maxlength="20" show-word-limit></el-input>
            </el-form-item>

            <el-form-item label=""  prop="description">
              <el-input type="textarea" autosize v-model="ruleForm.description" maxlength="100" show-word-limit></el-input>
            </el-form-item>

            <el-form-item label="" v-show="!uploaderShow">
              <img :src="imageUrl" 
              style="width:100%;"
                  alt class="showImage" 
                    accept="image/png, image/jpeg, image/gif, image/jpg" />
            </el-form-item>

            <el-form-item  label="" v-show="uploaderShow" >
              
              <el-upload
                ref="uploader"
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="true"
                :on-change="handleBeforeUpload"
                :before-remove="handleRemove"
                :limit=1
                :auto-upload="false">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                <div slot="tip" class="el-upload__tip">jpg/png5mb</div>
              </el-upload>
            </el-form-item>
                  
            <el-form-item>
              <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
              <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
          </el-form>
          
        </el-dialog>    
        <!--    -->
        <el-drawer
          :visible.sync="showUserInfo"
          direction="rtl" :show-close="false"
          @close="handleDialogClose(2)"
          >
          <span style="text-align: center;display:block;overflow: hidden;white-space:nowrap;"></span>
          <el-form label-position="left" :model="userInfo"  ref="userInfo" label-width="40%">
               <el-avatar size="large" style="left:50%;"
                    src="https://seusupermarket.cn:443/staticFile/Logo.png"></el-avatar>
                <!-- <el-form-item>                 
                </el-form-item> -->

                <el-form-item label="" prop="username">
                    <el-input v-model="userInfo.userName" :disabled="!changeUserinfo"></el-input>
                </el-form-item>
                <el-form-item label="ID" prop="userID">
                    <el-input v-model="userInfo.userID" :disabled="!changeUserinfo"></el-input>
                </el-form-item>
                <el-form-item label="" prop="username">
                    <el-input v-model="userInfo.userMail" :disabled="!changeUserinfo"></el-input>
                </el-form-item>
                <el-form-item label="" prop="username">
                    <el-input v-model="userInfo.onlineGoods" :disabled="!changeUserinfo"></el-input>
                </el-form-item>

                 <el-form-item>
                    <el-button @click="Logout"></el-button>
                    <el-button @click="handleSelect(6)"></el-button>
                </el-form-item>
            </el-form>
        </el-drawer>
      <!--  -->
       
        <!--  -->

      <div class="imgBack" >
      <el-row :gutter="20" class="elRowStyle" v-for="(it, row) in Math.ceil(picNum/4.0)" :key="it" >
        <el-col :span="6" v-for="(o, index) in (row+1)*4>picNum?(picNum-(row*4)):4" :key="o" >
          <el-card @click.native="showGoodsInfo(index+row*4)" class="cardStyle" :body-style="{ padding: '0px' }" shadow="hover">
            <img :src="picSrc[index+row*4]" class="image">
         
            <div>
              <span style="text-align: center;display:block;overflow: hidden;white-space:nowrap;">{{goodsName[index+row*4]}}</span>
              
              <span>{{""+goodsPrice[index+row*4]}}</span>
              <span>{{"     "+goodsCampus[index+row*4]}}</span>
            </div>
          </el-card>
        </el-col>
      </el-row>
      <span v-if="noGoods"></span>
      </div>
  
    <!--  -->
   <el-dialog title="" class="detailStyle" :visible.sync="showDetail" :modal="false">
            <el-form label-position="left" :model="detailGoodsInfo" :rules="detailRules" ref="detailGoodsInfo" label-width="40%">
                <el-form-item label="" prop="detailGoodsName">
                    <el-input v-model="detailGoodsInfo.detailGoodsName" @input="handleChangeInput" :disabled="!changeGoodsDetial"></el-input>
                </el-form-item>

                <!-- <el-form-item label="ID" prop="goodsID">
                    <el-input v-model="detailGoodsInfo.goodsID" :disabled="true"
                    @input="handleChangeInput"></el-input>
                </el-form-item> -->

                <el-form-item label="" prop="detailGoodsCampus">
                  <el-select v-model="detailGoodsInfo.detailGoodsCampus" :disabled="!changeGoodsDetial"
                  @change="handleChangeInput" >
                    <el-option
                      v-for="item in campusArray"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </el-form-item>

                <el-form-item label="" prop="detailGoodsPrice">
                    <el-input v-model="detailGoodsInfo.detailGoodsPrice" :disabled="!changeGoodsDetial"
                    @input="handleChangeInput"></el-input>
                </el-form-item>

                <el-form-item label="" prop="detailGoodsContact">
                    <el-input v-model="detailGoodsInfo.detailGoodsContact" :disabled="!changeGoodsDetial"
                    @input="handleChangeInput"></el-input>
                </el-form-item>
                
                <el-form-item label="" prop="detailGoodsDes">
                    <el-input type="textarea" v-model="detailGoodsInfo.detailGoodsDes" :autosize="true" 
                    @input="handleChangeInput" :disabled="!changeGoodsDetial"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
              <el-button @click="updateGoodsInfo('detailGoodsInfo')">{{buttonName}}</el-button>
              <el-button @click="deleteGoods(-1)"></el-button>
          </span>
      </el-dialog>   
      <!--  -->
        <el-dialog :append-to-body="true" :show-close="false" title="" 
        :close-on-click-modal="false" :visible.sync="dialogFormVisible" width="500px">
          <el-form >
            <el-form-item label="">
            </el-form-item>

            <el-form-item >
              <el-radio-group v-model="deleteReason">
                <el-radio :label="0"></el-radio>
                <el-radio :label="1"></el-radio>
                <el-radio :label="2"></el-radio>
              </el-radio-group>
            </el-form-item>
            
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="cancelDeleteGoods"> </el-button>
              <el-button type="primary" @click="sureDeleteGoods"> </el-button>
          </span>
        </el-dialog>

          <!--  -->
        <el-dialog :append-to-body="true" :show-close="false" title="" 
        :close-on-click-modal="false" :visible.sync="showChangePassword" width="500px">
          <el-form :model="changePassWordModel" :rules="changePasswordRules" ref="changePassWordModel" label-width="40%" >
            <!-- <el-form-item label="">
            </el-form-item> -->

            <el-form-item label="" prop="initPass">
              <el-input type="password" show-password v-model="changePassWordModel.initPass"></el-input>
            </el-form-item>

            <el-form-item label="" prop="password1">
              <el-input type="password" show-password v-model="changePassWordModel.password1"></el-input>
            </el-form-item>

            <el-form-item label="" prop="password2">
              <el-input type="password" show-password v-model="changePassWordModel.password2"></el-input>
            </el-form-item>
            
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="cancelChangePassword"> </el-button>
              <el-button type="primary" @click="sureChangePassword"> </el-button>
          </span>
        </el-dialog>

  </div>

</template>


<script>
import axios from 'axios';
import md5 from 'js-md5';
 export default({

    data: function(){
      var checkPrice=(rule,value,callback)=>
      {
          var patt=/^\d+(\.\d+)?$/;
          if (!patt.test(value))
          {
              callback(new Error(''));
          }
          var tempPrice=parseInt(value);
          if(tempPrice>6000){
            callback(new Error(" 6000"));
           }
          callback();
      };
      var checkPassword=(rule,value,callback)=>
      {
        let pattern = /^[a-zA-Z0-9]{8,20}$/;               
        if(!pattern.test(value))
        {
            callback(new Error('8-20'));
        }
        callback();
      };
      
    return {
      noGoods:false,
      fullscreenUpLoading:false,
      showChangePassword:false,
      changePassWordModel:{
        initPass:'',
        password1:'',
        password2:'',
      },
      
      showAddGoods:false,
      menuIndex:'2',
      showUserInfo:false,
        inputChanged:false,
        deleteReason:0,
        dialogFormVisible:false,
        currentGoodsID:0,
        buttonName:"",
        picNum:0,
        changeUserinfo:false,
        changeGoodsDetial:false,
        showDetail:false,
        useRouter:true,
        activeIndex: '/User',
        logState:false,
        userInfo:{
            userMail:'',
            userName:'',
            onlineGoods:0,
            userID:'',
        },
        campusArray: [{
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        }, {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        },
        {
          value: '',
          label: ''
        }],
        detailRules: {
          detailGoodsName: [
            { required: true, message: '', trigger: 'blur' },
            // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          detailGoodsPrice: [
            // { required: true, message: '' },
            //  { type: 'number', message: ''}
             {required:true,validator:checkPrice, trigger:'change'}
          ],
          detailGoodsCampus:[
             { required: true, message: ''},
          ],

          detailGoodsContact: [
            {  required: true, message: '', trigger: 'change' }
          ],
          detailGoodsDes: [
            {  required: true, message: '', trigger: 'change' }
          ],
        
        },
        
        detailGoodsInfo:{
          detailGoodsCampus:'',
          detailGoodsName:'',
          detailGoodsPrice:'',
          detailGoodsDes:'',
          detailGoodsContact:'',
          goodsID:'',
        },
        goodsName:[],
        goodsCampus:[],
        picSrc:[],
        goodsPrice:[],
        goodsDes:[],
        goodsID:[],
        goodsContact:[],
        
        curIndex:0,
        lastIndex:0,

        //addGoods
        uploaderShow:true,
        postUrl:'',


        // imgUrl:"",
        imageUrl:'',//el-uoload
        addArr:[],

        showAble:true,
         ruleForm: {
          name: '',
          price:'',
          contact: '',
          description: '',
          addFileName:'',
          goodsCampus:'',
            imgType:'',
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            // { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
          ],
          price: [
            // { required: true, message: '' },
            //  { type: 'number', message: ''}
             {required:true,validator:checkPrice,message: '', trigger:'change'}
          ],
          goodsCampus:[
             { required: true, message: ''},
          ],

          contact: [
            {  required: true, message: '', trigger: 'change' }
          ],
          description: [
            {  required: true, message: '', trigger: 'change' }
          ],
        
        },
        changePasswordRules:{
          initPass:[
            { required: true,validator:checkPassword, message: '8-20!', trigger: 'blur' }
          ],
          password1:[
            { required: true,validator:checkPassword, message: '8-20!', trigger: 'blur' }
          ],
          password2:[
            { required: true,validator:checkPassword, message: '8-20!', trigger: 'blur' }
          ],
        }
    };
    },
 watch:{

 },
   methods:{
     cancelChangePassword(){
       this.showChangePassword=false;
     },
    handleRemove(file,fileList){
      console.log(file, fileList)
        this.imageUrl='';
        this.uploaderShow=true;
      },   
     //
     handleDialogClose(index){
       if(index==1){
         this.menuIndex="2";
       }else if(index==2){
         this.showUserInfo=false;
       this.menuIndex="2";
       }
     },
     //
     handleSelect(index){
      if(index==1){
        this.menuIndex="1";//
        this.showUserInfo=true;   
      }else if(index==3){
        var maxGoods=parseInt(sessionStorage.getItem("maxGoods"));
         
        if(parseInt(this.userInfo.onlineGoods)>=maxGoods){
          this.menuIndex="2";//
          this.$message.error({message:"",duration:6000,showClose: true,});
          return;
        }else{
          this.menuIndex="3";//
          this.showAddGoods=true;
        }
      }else if(index==4){
        this.menuIndex="4";//
        this.showUserInfo=true;
      }else if(index==5){
        this.Logout();
      }else if(index==6){
        this.showChangePassword=true;
      }
     },
     //
     handleChangeInput(value){
      console.log(value);
       this.inputChanged=true;
     },
      //
     cancelDeleteGoods(){
        this.$message({
            type: 'info',
            message: ''
          });
        this.dialogFormVisible=false;
     },
     //
     sureDeleteGoods(){
         var goodsIDInfo={
            'goodsID':this.goodsID[this.curIndex],
            'userMail':sessionStorage.getItem("userMail"),
            'token':sessionStorage.getItem("token"),
            'reason':this.deleteReason
          }
           

          axios.post(process.env.URL_PATH+'/DeleteGoods',goodsIDInfo)
            .then((response) => {
                if(response.data['status']=="True")
                {
                  this.$message({
                        type: 'success',
                        message: '!'
                    });
                    this.detailGoodsInfo.detailGoodsName="";
                    this.detailGoodsInfo.detailGoodsPrice="";
                    this.detailGoodsInfo.detailGoodsCampus="";
                    this.detailGoodsInfo.detailGoodsContact='';
                    this.detailGoodsInfo.detailGoodsDes="";
                    this.detailGoodsInfo.goodsID="";
                    this.userInfo.onlineGoods=response.data['onlineGoods'];
                    if(this.userInfo.onlineGoods==0)
                    {
                      this.noGoods=true;
                    }else{
                      this.noGoods=false;
                    }
                    this.showDetail=false;
                }else
                {  
                  if(response.data['status']=="Error")
                  {
                    this.$message.error("");
                  }else if(response.data['status']=="False")
                  {
                    if(response.data['status']=="NotExistUser")
                    {
                       this.$message.error("");
                    }else if(response.data['status']=="NotExistGood")
                    {
                       this.$message.error("");
                    }
                  }else{
                    this.$message.error("");
                  }
                }
                this.updateUsersAllGoodsInfo();
            })
            .catch((err)=>{
                        console.log(err);
                        this.$message.error("");
                        }); 

          this.dialogFormVisible=false;
     },
     //
     sureChangePassword(){
        this.$refs['changePassWordModel'].validate((valid) => {
          if (valid) 
          {
              if((this.changePassWordModel.password1==this.changePassWordModel.password2)&&(this.changePassWordModel.password1!=this.changePassWordModel.initPass))
              {
                var formData = new FormData();
                //var md5Password=md5(this.info.password)+'piao';
                var initPass=md5(this.changePassWordModel.initPass);
                initPass=initPass.substr(0,16)+"piao"+initPass.substr(16,16);

                var newPassword=md5(this.changePassWordModel.password1);
                newPassword=newPassword.substr(0,16)+"piao"+initPass.substr(16,16);
                formData.append('initPass',initPass);
                formData.append('newPassword',newPassword);

                formData.append('token',sessionStorage.getItem("token"));
                formData.append('userMail',sessionStorage.getItem("userMail"));

                let config = {
                  headers: {
                    'Content-Type': 'multipart/form-data',
                    'Authorization': this.token
                  }
                };

                axios.post(process.env.URL_PATH+'/User/UpdateUserPassword',formData,config)
                  .then((response) => {
                    console.log(response.data['status']);
                      if(response.data['status']=="True"){this.$message({
                              type: 'success',
                              message: '!'
                          });
                          this.Logout();
                      }else if(response.data['status']=="Error"){               
                        this.$message.error("");
                      }else if(response.data['status']=="NotExistUser")   
                      {
                        this.$message.error("");
                      }else if(response.data['status']=="passCheckFalse")   
                      {
                        this.$message.error("");
                      }else{
                        this.$message.error("");
                      }

                  })
                  .catch((err)=>{
                            console.log(err);
                            this.$message.error("");
                            }); 
              } 
              else if(this.changePassWordModel.password1==this.changePassWordModel.initPass) {
                this.$message.error("");
              }else{
                  this.$message.error("");
              }
          }
          else
          {
            this.$message.error("");
          }
      });
     },
     //dia
     deleteGoods(index){
       this.curIndex=index;
        if(index==-1){
          index=this.currentGoodsID;
          this.curIndex=index;
        }
        this.showDetail=false;
        this.dialogFormVisible=true;
      
     },
     //
     updateGoodsInfo(formName){
       if(this.buttonName==""){
         this.buttonName="";
         this.changeGoodsDetial=true;
       }else{
          this.buttonName="";
          this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.inputChanged==true){
              var formData = new FormData();
              //formData.append('filename',this.detailGoodsInfo.detailGoodsName);
              formData.append('price',this.detailGoodsInfo.detailGoodsPrice);
              formData.append('description',this.detailGoodsInfo.detailGoodsDes);
              formData.append('contact',this.detailGoodsInfo.detailGoodsContact);
              formData.append('token',sessionStorage.getItem("token"));
              formData.append('userMail',sessionStorage.getItem("userMail"));
              formData.append('name',this.detailGoodsInfo.detailGoodsName);
              //formData.append('filedata',this.addArr[0]);
              formData.append('campus',this.detailGoodsInfo.detailGoodsCampus);
              formData.append('goodsID',this.goodsID[this.currentGoodsID]);
              let config = {
              headers: {
                'Content-Type': 'multipart/form-data',
                'Authorization': this.token
              }
              };

            axios.post(process.env.URL_PATH+'/UpdateGoodsInfo',formData,config)
              .then((response) => {
                  if(response.data['status']=="True"){this.$message({
                          type: 'success',
                          message: '!'
                      });
                      this.showDetail=false;
                      this.inputChanged=false;
                      this.updateUsersAllGoodsInfo();
                  }else if(response.data['status']=="Error"){          
                       
                    this.$message.error("");
                  }else if(response.data['status']=="NotExistUser")   
                  {
                    this.$message.error("");
                  }else{
                    this.$message.error("");
                  }
              })
              .catch((err)=>{
                          console.log(err);
                          this.$message.error("");
                          }); 
            } else {
              this.inputChanged=false;
              this.changeGoodsDetial=false;
            }
          }else{
            this.$message.error("");
            this.detailGoodsInfo.detailGoodsName=this.goodsName[this.curIndex];
            this.detailGoodsInfo.detailGoodsPrice=this.goodsPrice[this.curIndex];
            this.detailGoodsInfo.detailGoodsDes=this.goodsDes[this.curIndex];
            this.detailGoodsInfo.detailGoodsContact=this.goodsContact[this.curIndex];
            this.detailGoodsInfo.detailGoodsCampus=this.goodsCampus[this.curIndex];
            this.changeGoodsDetial=false;
            this.buttonName="";
          }
        });
          this.changeGoodsDetial=false;
       }
       
     },
      //dialog
      showGoodsInfo(index){
        this.inputChanged=false;
        this.currentGoodsID=index;
        this.changeGoodsDetial=false;
        this.buttonName="";
        this.detailGoodsInfo.detailGoodsName=this.goodsName[index];
        this.detailGoodsInfo.detailGoodsPrice=this.goodsPrice[index];
        this.detailGoodsInfo.detailGoodsDes=this.goodsDes[index];
        this.detailGoodsInfo.detailGoodsContact=this.goodsContact[index];
        this.detailGoodsInfo.detailGoodsCampus=this.goodsCampus[index];
        this.detailGoodsInfo.goodsID=this.goodsID[index];
        if(this.lastIndex==index){
            if(this.showDetail==true){
              this.showDetail=false;
            }else{
            this.showDetail=true;
            }
        }else{
          this.showDetail=true;
          this.lastIndex=index;
        }
      
      },
      //
      Logout(){
        sessionStorage.removeItem('logState');
        sessionStorage.removeItem('userMail','');
        
        this.$router.push("/");
      },
      //
      updateUsersAllGoodsInfo(){
        var getUserGoodsInfo=sessionStorage.getItem("getUserGoodsInfo");
        if(getUserGoodsInfo==null){
            var userInfo={
                     "token":sessionStorage.getItem("userMail"),
                 }
               this.goodsID=[],
               this.goodsContact=[],
               this.goodsName=[],
              this.goodsCampus=[],
                this.picSrc=[],
                this.goodsPrice=[],
                this.goodsDes=[],
                this.goodsID=[],
                this.goodsContact=[],
                 axios
                 .post(process.env.URL_PATH+"/User/getUserGoodsInfo",userInfo)
                 .then((res)=>{
                     if(res.data['status']=='true'){
                      //sessionStorage.setItem("getUserGoodsInfo",true);
                      this.picNum=parseInt(res.data['totalNum']);
                      var info=res.data['userGoodsInfo'];

                      for(var i=0;i<info.length;i++){

                        this.goodsID.push(info[i][0]);
                        this.goodsName.push(info[i][1]);
                        this.goodsCampus.push(info[i][3]);
                        this.goodsPrice.push(info[i][4]);
                        this.goodsDes.push(info[i][5]);
                        this.goodsContact.push(info[i][6]);
                       
                        this.picSrc.push("https://seusupermarket.cn:443"+"/goodsPicFileSystem/ZIPpics/"+info[i][2])
                       
                     }
                     } else{
                       this.$message.error("");
                     } 
                     if(this.picNum==0)
                     {
                       this.noGoods=true;
                     }else{
                       this.noGoods=false;
                     }
                 })
                 .catch((err)=>{
                     this.$message.error("",err);
                     })   
         }
      },
      //
      handleBeforeUpload(file,fileList){
        if(file!=undefined)      
           { 
             this.uploaderShow=false;
             //console.log(file);
              var imgName=file.name;
              this.ruleForm.addFileName=imgName;
              var idx = imgName.lastIndexOf("."); 
              if (idx != -1){
                  var ext = imgName.substr(idx+1).toUpperCase();   
                  ext = ext.toLowerCase( ); 
                  //this.$message.error(ext);
                  if(ext=='jpg'||ext=='png')
                  {
                    if(file.size>5242880){
                      this.$message.error("5MB");
                      console.log(fileList);
                      fileList.pop();
                      this.uploaderShow=true;
                      return
                    }
                    else{
                      this.addArr.push(file.raw);
                      this.ruleForm.imgType=ext;
                    }
                  }else{
                    this.$message.error("");
                    return ;
                  }
                  
  
              }
              else{
                return;
              }
              var reader = new FileReader();
              reader.readAsDataURL(file.raw); //base64
              reader.onload = () => {
              const ImgBase64 = reader.result;
              // base64src
              this.imageUrl = ImgBase64;
              };
          }
      },
        //
       submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //alert('submit!');
            if(this.ruleForm.addFileName==""||0==this.addArr.length)
            {
             this.$message({
               type: 'error',
               message: ''
             });
             return;
           }
          
            // this.fullscreenUpLoading=true;
          const loading = this.$loading({
            lock: true,
            text: '',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
            var formData = new FormData();
            formData.append('filename',this.ruleForm.addFileName);
            formData.append('price',this.ruleForm.price);
            formData.append('description',this.ruleForm.description);
            formData.append('contact',this.ruleForm.contact);
            formData.append('type',this.ruleForm.imgType);
            formData.append('name',this.ruleForm.name);
            formData.append('filedata',this.addArr[0]);
            formData.append('campus',this.ruleForm.goodsCampus);
            formData.append("token",sessionStorage.getItem("token"))
             let config = {
            headers: {
              'Content-Type': 'multipart/form-data',
              'Authorization': this.token
            }
          };

          axios.post(process.env.URL_PATH+'/Uploadfile/'+sessionStorage.getItem('userMail'),formData,config)
            .then((response) => {
                if(response.data['status']=="True"){this.$message({
                        type: 'success',
                        message: '!'
                    });
                    this.showAddGoods=false;
                    this.ruleForm.name=(parseInt(this.ruleForm.name)+1).toString();
                    this.userInfo.onlineGoods=parseInt(response.data['onlineGoods']);

                    this.noGoods=false;

                    sessionStorage.setItem("onlineGoods",response.data['onlineGoods']);
                    this.menuIndex="2";
                    //this.showAddGoods=false;
                    this.updateUsersAllGoodsInfo();                }
                else{                
                  if(response.data['status']=="Over"){
                      this.$message.error({message:"",duration:6000,showClose: true,});
                  }else{
                    this.$message.error("!");
                  }
                }
            // this.fullscreenUpLoading=false;
            loading.close();
                //  this.$refs['ruleForm'].resetFields();
                //  this.resetForm();
            })
            .catch((err)=>{
                        console.log(err);
                        this.$message.error("");
                        this.$refs['ruleForm'].resetFields();
                        this.resetForm();
                        }); 
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      //
      resetForm(formName) {
        console.log(formName);
        this.ruleForm.addFileName='';

        this.addArr[0]='';
        this.addArr=[];
        this.imageUrl='';
        this.$refs.uploader.clearFiles();
        this.uploaderShow=true;
      },
         
   },
   mounted() {
   
    if(sessionStorage.getItem('logState')!='true'){
      this.$message.error('');
      sessionStorage.setItem("lastPage","User");
      this.$router.push("/");
    }else{
      this.logState=sessionStorage.getItem('logState');
      this.userInfo.userMail=sessionStorage.getItem("userMail");
      this.userInfo.userName=sessionStorage.getItem("userName");
      this.userInfo.onlineGoods=parseInt(sessionStorage.getItem("onlineGoods"));
      if(this.userInfo.onlineGoods==0)
      {
        this.noGoods=true;
      }else{
        this.noGoods=false;
      }
      this.userInfo.userID=sessionStorage.getItem("userID");
      this.postUrl=process.env.URL_PATH+'/Uploadfile/'+sessionStorage.getItem('userMail');
    }
  
    this.updateUsersAllGoodsInfo();
    }
     
 })

</script>

